.container {
    margin: 200px 20px 0 20px;
    padding: 20px;
    border: solid 1px black;
    background-color: lightgray;
    border-radius: 6px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-height: 400px;
}

.left-side {
    padding: 0 30px 0 10px;
    border-right: 1px solid gray;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.notification-wrapper {
    display: flex;
    flex-direction: column;
}

.notification-item {
    border: 1px solid gray;
    padding: 10px 8px;
    border-radius: 8px;
    background-color: white;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
}

.footer--btn {
    display: flex;
    width: 93px;
    height: 36px;
    border-radius: 7px;
    border: 2px solid;
    flex-direction: row;
    align-items: stretch;
    background-color: transparent;
}

input[value="Clear"] {
    text-align: center;
    display: flex;
    font-size: 17px;
    justify-content: center;
}


.notification__description-box {
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    justify-content: space-between;
    min-height: 45px;
}

.footer {
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    justify-content: space-between;
}

.right-side {
    background-color: lightgray;
    width: 40%;
}

.right-side--header {
    font-size: 30px;
    font-family: "Times New Roman", Times, serif;
    margin-left: 19px;
    color: black;
}

.calendar {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 6px;
    margin-left: 19px;
    justify-content: space-around;
    align-content: space-around;
    border: 1px solid gray;
    /*flex-wrap:wrap;*/
}

.calendar--header__week-day {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    padding: 0;
    justify-content: space-around;
    background-color: white;
}

.calendar__header__week-day--day {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 0;
    font-size: 12px;
    justify-content: space-between;
}

.calendar__header__month {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 9px 0 0 0;

}

.calendar__header__month--title {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    color: black;
}

.calendar__days {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    list-style-type: none;
}

.calendar__header__month--right-arrow {

}

calendar__header__month--left-arrow {
}

.calendar__days__day {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 14%;
    align-items: center;

}

.calendar__days__day--event {
    background-color: gray;
    border-radius: 45px;
    width: 3px;
    height: 3px;
    margin-top: 4px;
}

.disabled {
    pointer-events: none;
    opacity: 0.6;
    list-style-type: none;
}
